<template>
    <!-- <v-layout red :style="styles">
        <GChart
        type="BarChart"
        :data="chartData"></GChart>
    </v-layout> -->
    <v-layout column>
        <v-layout tag="div" id="chart_div" pb-4 pt-2>
        </v-layout>
        <v-divider></v-divider>        
    </v-layout>
</template>

<script>
import { GChart } from 'vue-google-charts'

export default {
    components: { GChart },
    props: {
        susceptibility: {
            type: Array
        }
    },
    created() {
      // Set a callback to run when the Google Visualization API is loaded.
      google.charts.setOnLoadCallback(this.drawChart);
    },
    computed: {
        chartData() {
            const data = this.susceptibilities.map(susceptibility => {
                return [
                    this.cropText(susceptibility.antibiotic),
                    susceptibility.isolates*Math.floor(Math.random()*10)+Math.floor(Math.random()*10),
                    susceptibility.isolates*Math.floor(Math.random()*10)+Math.floor(Math.random()*10),
                    susceptibility.isolates*Math.floor(Math.random()*10)+Math.floor(Math.random()*10),
                ]
            })
            data.unshift(['Antibiotic(s)', 'Susceptible', 'Intermediate', 'Resistant'])
            data.pop()
            data.pop()
            data.pop()
            return data
        },
        styles() {
            const heigth = this.susceptibilities.length * 80
            return `width: 100%; height: ${heigth}px`
        },
        legendPosition() {
            return this.$vuetify.breakpoint.mdAndDown ? 'none' : 'top'
        }
    },
    data() {
        return {
            susceptibilities: [
                {antibiotic: "Amikacin", antibioticClass: "Aminoglycosides", isolates: "39", susceptibility: "100", label: "SUSCEPTIBLE"},
                {antibiotic: "Gentamicin", antibioticClass: "Aminoglycosides", isolates: "33", susceptibility: "83.3", label: "SUSCEPTIBLE"},
                {antibiotic: "Tobramycin", antibioticClass: "Aminoglycosides", isolates: "60", susceptibility: "92.3", label: "SUSCEPTIBLE"},
                {antibiotic: "Pipercilli np Tozobactum", antibioticClass: "B-Lactams", isolates: "47", susceptibility: "100", label: "SUSCEPTIBLE"},
                {antibiotic: "Ciprofloxocin", antibioticClass: "Quinolones", isolates: "19", susceptibility: "30.8", label: "RESISTANT"},
                {antibiotic: "Ceftazidime", antibioticClass: "Cephalosporins", isolates: "99", susceptibility: "100", label: "SUSCEPTIBLE"},
            ]
        }
    },
    methods: {
        cropText(str) {
            if (str.length > 10) {
                return str.substring(0, 10) + "..."
            }
            return str
        },
        selectHandler() {
            var selectedItem = chart.getSelection()[0];
            var value = data.getValue(selectedItem.row, 0);
            alert('The user selected ' + value);
        },
        drawChart() {
            var data = new google.visualization.arrayToDataTable(this.chartData);
            
            var options = {
                animation: {
                    startup: true,
                    duration: 1000,
                    easing: 'in',
                },
                chart: {
                    title: "Antibiotic Susceptibility",
                    titlePosition: 'in',
                },
                titlePosition: 'in',
                titleTextStyle: {
                    fontSize: 15
                },
                tooltip: {
                    ingonoreBounds: true
                },
                annotations: {
                    alwaysOutside: true,
                },                
                legend: {
                    position: this.legendPosition
                },
                chartArea: {
                    top: 0,
                },
                height: '400',
                'width': '100%',
                'bars': 'horizontal',
                isStacked: false,
                'hAxis': {
                    'title': '# Isolates/Patients',
                    'format': 'decimal',
                    // baselineColor: 'pink',
                    // textStyle: {
                    //     color: 'green',
                    //     fontSize: '25px',
                    //     italic: false,
                    //     bold: true,
                    // }
                },
                'vAxis': {
                    'title': 'Antibiotics',
                    maxValue: 20,
                    viewWindowMode: 'maximized'
                },
            }

            // Instantiate and draw our chart, passing in some options.
            const chart = new google.charts.Bar(document.getElementById('chart_div'));
            // google.visualization.events.addListener(chart, 'select', this.selectHandler);
            chart.draw(data, google.charts.Bar.convertOptions(options));
        }
    }
}
</script>

